import React, { Component } from 'react'
import PropTypes from 'prop-types'
import { Row, Col, Icon, Dropdown, Menu } from 'antd'
import { css } from 'emotion' 
import Container from '../components/Container'

class Globalnav extends Component {
  constructor(props) {
    super(props)
    this.state = {
      toggle: false
    }
  }

  render() {
    let { toggle } = this.state
    let settingsDropdown = (
      <Menu>
        <Menu.Item>
          <a target="_blank" rel="noopener noreferrer" href="http://www.alipay.com/">1st menu item</a>
        </Menu.Item>
        <Menu.Item>
          <a target="_blank" rel="noopener noreferrer" href="http://www.taobao.com/">2nd menu item</a>
        </Menu.Item>
        <Menu.Item>
          <a target="_blank" rel="noopener noreferrer" href="http://www.tmall.com/">3rd menu item</a>
        </Menu.Item>
      </Menu>
    )

    let rotate = toggle ? 180 : 0
    return (
      <nav className={css`
        position: absolute;
        left: 0;
        right: 0;
        top: 0;
        height: 60px;
        border-bottom: 1px solid #efefef;
        background: #fff;
        z-index: 99999;
      `}>
        <Container xl>
          <Row type="flex" align="middle" justify="space-between" className={css`
            height: 60px;
          `}>
            <Col span={6}>
              <h2>Muhammad Rusdi</h2>
            </Col>
            <Col span={8}>
              <div>
                <form action="" className={css`
                  margin: 0;
                `}>
                  <input type="text" placeholder="Search" className={css`
                    width: 100%;
                    border: none;
                    padding: 10px;
                    background: #efefef;
                    outline: none;
                  `} />
                </form>
              </div>
            </Col>
            <Col span={6}>
              <Row type="flex" justify="end" align="center">
                <Col>
                  <Dropdown overlay={settingsDropdown} trigger={['click']} onVisibleChange={
                    (visible => this.setState({toggle: visible}))
                  }>
                  
                    <Icon type="down" className={css`
                      transition: transform .3s ease;
                      transform: rotateX(${rotate}deg);
                    `}/>
                  </Dropdown>
                </Col>
              </Row>
            </Col>
          </Row>
        </Container>
      </nav>
    )
  }
}

export default Globalnav


